<template>
  <!-- 视频  mv页 -->
  <div class="mvlist">
    <div class="mvlist__head">
      <div class="mvlist__head__area">
        <div class="mvlist__head__area__title">地区</div>
        <div class="mvlist__head__area__container">
          <div :class="{mvlist__head__area__container__item: true, 'mvlist__head__area__container__item-active': item == currentArea}"
          v-for="item in area"
          @click ="getData(item,currentType,currentOrder)"
          :key="item">
            {{ item }}
          </div>
        </div>
      </div>
      <div class="mvlist__head__type">
        <div class="mvlist__head__type__title">类型</div>
        <div class="mvlist__head__type__container">
          <div :class="{mvlist__head__type__container__item: true, 'mvlist__head__type__container__item-active': item == currentType}"
          v-for="item in type"
          @click ="getData(currentArea,item,currentOrder)"
          :key="item">
            {{ item }}
          </div>
        </div>
      </div>
      <div class="mvlist__head__order">
        <div class="mvlist__head__order__title">排序</div>
        <div class="mvlist__head__order__container">
          <div :class="{mvlist__head__order__container__item: true, 'mvlist__head__order__container__item-active': item == currentOrder}"
          v-for="item in order"
          @click ="getData(currentArea,currentType,item)"
          :key="item">
            {{ item }}
          </div>
        </div>
      </div>
    </div>
    <div class="mvlist__list">
      <MVCoverVue v-for="item in mvList"
      :key="item.id"
      :item="item"></MVCoverVue>
    </div>
  </div>
</template>

<script>
import { getMV } from '../../api/api_video'
import MVCoverVue from './MVCover.vue'
export default {
  name: 'MVList',
  components: { MVCoverVue },
  data () {
    return {
      mvList: null,
      currentArea: '全部',
      currentType: '全部',
      currentOrder: '上升最快',
      area: [
        '全部',
        '内地',
        '港台',
        '欧美',
        '韩国',
        '日本'
      ],
      type: ['全部', '官方版', '现场版', '原生', '网易出品'],
      order: ['上升最快', '最热', '最新']
    }
  },
  methods: {
    async getData (area, type, order) {
      [this.currentArea, this.currentType, this.currentOrder] = [area, type, order]
      const res = await getMV(this.currentArea, this.currentType, this.currentOrder)
      this.mvList = res.data.data
    }
  },
  created () {
    this.getData(this.currentArea, this.currentType, this.currentOrder)
  }
}
</script>

<style lang="scss" scoped>
.mvlist{
  padding: 0 20px;
  &__head{
    &__area{
      display: flex;
      align-items: center;
      margin-bottom: 12px;
      &__title{
        margin-right: 20px;
        font-size: 12px;
      }
      &__container{
        display: flex;
        align-items: center;
        &__item{
          margin-right: 30px;
          padding: 0 8px;
          height: 16px;
          border-radius: 8px;
          font-size: 12px;
          color: #7f7f7f;
          cursor: pointer;
          &-active{
            color: red;
            background-color: #fdebeb;
          }
        }
      }
    }
    &__type{
      display: flex;
      align-items: center;
      margin-bottom: 12px;
      &__title{
        margin-right: 20px;
        font-size: 12px;
      }
      &__container{
        display: flex;
        align-items: center;
        &__item{
          margin-right: 30px;
          padding: 0 8px;
          height: 16px;
          border-radius: 8px;
          font-size: 12px;
          color: #7f7f7f;
          cursor: pointer;
          &-active{
            color: red;
            background-color: #fdebeb;
          }
        }
      }
    }
    &__order{
      display: flex;
      align-items: center;
      margin-bottom: 12px;
      &__title{
        margin-right: 20px;
        font-size: 12px;
      }
      &__container{
        display: flex;
        align-items: center;
        &__item{
          margin-right: 30px;
          padding: 0 8px;
          height: 16px;
          border-radius: 8px;
          font-size: 12px;
          color: #7f7f7f;
          cursor: pointer;
          &-active{
            color: red;
            background-color: #fdebeb;
          }
        }
      }
    }
  }
  &__list{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid: 20px;
  }
}
</style>
