import { get } from '../utils/request'
// 获取全部视频
export const getAllVideo = (offset = 30) => get('/video/timeline/all', { timestape: Date.now(), offset: offset })
// 获取标签列表
export const getTagList = () => get('/video/group/list', {})
// 获取视频分类
export const getCateList = () => get('/video/category/list', {})
// 获取相关标签的视频
export const getTagVideo = (id) => get('/video/group', { id: id, timestape: Date.now() })
// 获取推荐视频
export const getRecommendVideo = () => get('/video/timeline/recommend')
// 获取视频地址
export const getVideoUrl = (vid) => get('/video/url', { id: vid, timestape: Date.now() })
// 获取mv
export const getMV = (area = '全部', type = '全部', order = '上升最快') => get('/mv/all', { area: area, type: type, order: order })
// mv地址
export const getMVUrl = (id) => get('/mv/url', { id: id })
